@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,400;0,700;1,400;1,700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  @variants responsive {
    .clip-peak-4 {
      clip-path: polygon(0 1rem, 50% 0, 100% 1rem, 100% 100%, 0 100%);
    }
    .clip-peak-16 {
      clip-path: polygon(0 4rem, 50% 0, 100% 4rem, 100% 100%, 0 100%);
    }
    .clip-peak-24 {
      clip-path: polygon(0 6rem, 50% 0, 100% 6rem, 100% 100%, 0 100%);
    }
  }

  .shadow-fire {
    box-shadow: 0 0 0.8rem #e2db9e;
  }
}
